.main {
  padding: 10px;
  height: 100vh;
  background-color: rgb(212, 217, 227);
}
.layout {
  display: flex;
  /* background-color: rgb(212, 217, 227); */
}

.sidebar {
  min-height: 100%;
  width: 300px;
  border-radius: 5px;
  background-color: rgb(89, 130, 207);
  box-shadow: 0 0 2px gray;
  margin-right: 20px;
  color: rgb(255, 255, 255);
}

.content {
  width: 100%;
  height: 100%;
  background-color: rgb(212, 217, 227);
}

.header {
  height: 10vh;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color:  #e4f1fe;
}
.body {
  height: 100%;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color:  #e4f1fe;
}
.logo h6 {
  font-size: 1.5rem;
  text-align: center;
  margin: 20px 0px;
}
.menu {
  margin-top: 50px;
}
.menu-item {
  margin-top: 30px;
}
.menu-item a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;

}

.menu-item i {
  font-size: 1.2rem;
  margin: 0 15px;
}
.active {
  background-color: rgb(187, 209, 233);
  padding: 10px;
  transition: all 0.4s;

}
.active a,
.active i {
  color: rgb(51, 1, 1);
}
.menu-item:hover{
  background-color:rgb(187, 209, 233) ;
  padding: 7px;
  transition: 0.4s;
  color: black;
}
a:hover{
  background-color:rgb(187, 209, 233) ;
  padding: 5px;
  transition: 0.3s;
  color: black;
  font-size: 1.2rem;
}

.header-content {
  display: flex;
  align-items: center;
  height: 50px;
  justify-content: flex-end;
  margin: 0 15px;
}

.header-content i {
  margin-right: 20px;
  font-size: 1.2rem;
}
.header-content a {
  text-decoration: none;
  font-size: 1.2rem;
  color: blue;
  margin: 0 10px;
  text-transform: uppercase;
}
.card-header:hover{
  background-color:rgb(187, 209, 233);
  padding: 15px;
  transition:0.3s ;
}

@media(min-width:350px) and (max-width:768px){  
  .ant-table-cell{  
    font-size: 5px;    
  }
}


@media (min-width: 768px) and (max-width:944px){
.ant-table-cell{

    font-size: 10px;    
  }
}
@media (max-width: 768px) {
  .layout {
    flex-direction: column;
  }

  .sidebar {
    display: flex;
    width: 100%;
    min-height: auto;
    margin-right: 0;
    margin-bottom: 20px;
    border-radius: 0;
  }

  .content {
    width: 100%;
  }

  .header {
    height: auto;
    padding: 10px;
  }

  .body {
    height: auto;
    padding: 10px;
  }

  .header-content {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
  }

  .menu {
    display: flex;
    gap: 10px;
    margin: 0px;
     padding-right: 5px;
    
  }

  .menu-item {
    margin-top: 0px;
    padding: 5px;
   
    height: auto;
  }

  .logo h6 {
    font-size: 0px;
  }

  .menu-item a {
    font-size: 0.8rem;
  }

  .menu-item i {
    font-size: 1rem;
    margin: 0 10px;
  }
  .card{
    width: 100%;
  }

  .header-content i,
  .header-content a {
    font-size: 1rem;
    margin: 5px 0;
  }
 
}
 @media (min-width: 768px) and (max-width:1018px) {
  .card{
    display: flex;
    justify-content: flex-start;
    flex-basis: 47%;
  } 
 } 
  @media (min-width: 1018px) and (max-width:1300px) {
  .card{
    display: flex;
    justify-content: flex-start;
    flex-basis: 31%;
  } 
 } 

